













import Vue from 'vue';

export default Vue.extend({
    name: 'LocaleChoice',
    data: () => ({
        availableLanguages: ['en', 'pl'],
    }),
    methods: {
        setLocale(locale: string) {
            this.$i18n.locale = locale;
        },
        getCurrentLocale(): string {
            return this.$i18n.locale;
        },
    },
});
